export default {
    async fetch() { //server 
        try {
            await this.$store.dispatch('category/getCategory', 'product');
        } catch (error) {
            console.log(error);
        }
    },
    created() {
        if(process.client) this.$store.dispatch('cart/getLocalStorageCart');
    },
};
