import { mapState } from 'vuex';
export default {
    computed: {
        data() {
            return {
                profileDropDown: false
            };
        },
        ...mapState({
            category: state => {
                return state.category.category;
            },
            cart: state => state.cart.cart
        })
    },
    methods: {
        async logout() {
            try {
                await this.$auth.logout();
                this.$router.push({ name: 'home-page' });
            } catch (error) {
                console.log(error);
            }
        }
    }
};
